import { h, Component } from 'preact';
import { init } from 'pell';
import 'pell/dist/pell.css';
import styles from './styles.scss';

export default class Editor extends Component {

    editor = null

    image = require('./../../assets/images/icons.svg')

    constructor (props) {
        super(props)
        this.state = { html: null }
    }

    componentDidMount () {
        this.editor = init({
            element: document.getElementById('editor'),
            onChange: html => this.setState({ html }),
            classes: {
                actionbar: styles["luna-editor-actionbar"] + " luna__card",
                button: 'pell-button',
                content: 'pell-content',
                selected: 'pell-button-selected'
            },
            actions: [
                {
                    name: 'bold',
                    icon: `
                        <svg 
                        viewBox="0 0 50 50"
                        width="18" height="18"
                        role="presentation">
                        <use xlink:href="${this.image}#luna__bold"></use>
                        </svg>
                    `
                },
                {
                    name: 'underline',
                    icon: `
                        <svg 
                        viewBox="0 0 50 50"
                        width="16" height="16"
                        role="presentation">
                        <use xlink:href="${this.image}#luna__underline"></use>
                        </svg>
                    `
                },
                {
                    name: 'italic',
                    icon: `
                        <svg 
                        viewBox="0 0 50 50"
                        width="16" height="16"
                        role="presentation">
                        <use xlink:href="${this.image}#luna__italic"></use>
                        </svg>
                    `
                },
                {
                    name: 'strikethrough',
                    icon: `
                        <svg 
                        viewBox="0 0 50 50"
                        width="15" height="15"
                        role="presentation">
                        <use xlink:href="${this.image}#luna__strikethrough"></use>
                        </svg>
                    `
                },
                {
                    name: 'quote',
                    icon: `
                        <svg 
                        viewBox="0 0 50 50"
                        width="16" height="16"
                        role="presentation">
                        <use xlink:href="${this.image}#luna__quote"></use>
                        </svg>
                    `
                },
                {
                    name: 'olist',
                    icon: `
                        <svg 
                        viewBox="0 0 50 50"
                        width="20" height="20"
                        role="presentation">
                        <use xlink:href="${this.image}#luna__numbered_list"></use>
                        </svg>
                    `
                },
                {
                    name: 'ulist',
                    icon: `
                        <svg 
                        viewBox="0 0 50 50"
                        width="20" height="20"
                        role="presentation">
                        <use xlink:href="${this.image}#luna__bulleted_list"></use>
                        </svg>
                    `
                },
                {
                    name: 'code',
                    icon: `
                        <svg 
                        viewBox="0 0 50 50"
                        width="20" height="20"
                        role="presentation">
                        <use xlink:href="${this.image}#luna__code"></use>
                        </svg>
                    `
                },
                {
                    name: 'line',
                    icon: `
                        <svg 
                        viewBox="0 0 50 50"
                        width="18" height="18"
                        role="presentation" transform="rotate(-90)">
                        <use xlink:href="${this.image}#luna__separator"></use>
                        </svg>
                    `
                },
                {
                    name: 'image',
                    icon: `
                        <svg 
                        viewBox="0 0 50 50"
                        width="20" height="20"
                        role="presentation">
                        <use xlink:href="${this.image}#luna__image"></use>
                        </svg>
                    `,
                    result: () => {
                        const url = window.prompt('Enter the image URL')
                        if (url) window.pell.exec('insertImage', ensureHTTP(url))
                    }
                },
                {
                    name: 'link',
                    icon: `
                        <svg 
                        viewBox="0 0 50 50"
                        width="20" height="20"
                        role="presentation">
                        <use xlink:href="${this.image}#luna__link"></use>
                        </svg>
                    `,
                    result: () => {
                        const url = window.prompt('Enter the link URL')
                        if (url) window.pell.exec('createLink', ensureHTTP(url))
                    }
                }
            ],
        })
    }

    render() {
        return (
            <div>
                <div>I am Editor</div>
                <div id="editor" className="pell"></div>
            </div>
        );
    }

}